import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Sidebar from "../Sidebar";
import pdflogo from "../../resources/pdf/download.png";
import "./style.css";

const PDFUploadPage = () => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [uploadedFileName, setUploadedFileName] = useState(null);
  const [isFileUploaded, setIsFileUploaded] = useState(false);

  const navigate = useNavigate();

  const handleButtonClick = async () => {
    await handleFileUpload(); //  handleFileUpload
    handleSendClick(); //  handleSendClick
  };

  const handleSendClick = () => {
    //    "Send"
    //          isFileUploaded
    // :
    //      :
    setIsFileUploaded(true);
    navigate("/unloading"); //     Unloading
  };

  const handleFileChange = (event) => {
    setSelectedFile(event.target.files[0]);
    setUploadedFileName(event.target.files[0]?.name || null);
  };

  const handleFileUpload = async () => {
    if (!selectedFile) return;

    const formData = new FormData();
    formData.append("pdfFile", selectedFile);

    try {
      const response = await fetch("/upload-pdf", {
        method: "POST",
        body: formData,
      });

      if (response.ok) {
        //   ,      
      } else {
        console.error("Error uploading PDF");
      }
    } catch (error) {
      console.error("Error uploading PDF", error);
    }
  };

  return (
    <div className="pdf-upload-page">
      <Sidebar />
      <div className="content">
        <div className="centered-container">
          <div className="file-upload-container">
            <label className="file-input-label">
              <div className="file-input-container">
                <input
                  type="file"
                  accept=".pdf"
                  onChange={handleFileChange}
                  className="file-input"
                />
                <img src={pdflogo} alt="download" className="file-input-icon" />
              </div>
              {uploadedFileName && (
                <div className="file-info">
                  <p>Uploaded: {uploadedFileName}</p>
                </div>
              )}
              <button
                className="btn btn-lg btn-unloading"
                onClick={handleButtonClick}
              >
                Send
              </button>
            </label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PDFUploadPage;
